.divider:after, .divider:before {
  content: "";
  background: #eee;
  flex: 1;
  height: 1px;
}

.h-custom {
  height: calc(100% - 73px);
}

@media (width <= 450px) {
  .h-custom {
    height: 100%;
  }
}

header {
  color: #fff;
  z-index: 100;
  z-index: 100;
  background: linear-gradient(90deg, #007bff, #00bcd4);
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  font-family: Montserrat, sans-serif;
  transition: all .3s ease-in-out;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 6px #0000001a;
}

header .logo a {
  text-decoration: none;
}

header .logo h2 {
  letter-spacing: 1.5px;
  color: #fff;
  margin-bottom: 0;
  padding-top: 0;
  font-family: Lora, serif;
  font-size: 2.2rem;
  font-weight: 700;
  transition: transform .3s ease-in-out;
}

header .logo h2:hover {
  color: #fff;
  transform: scale(1.1);
}

header .btn {
  color: #fff;
  text-transform: uppercase;
  background-color: #fff;
  border: none;
  border-radius: 50px;
  margin-top: 0;
  padding: 8px 18px;
  font-family: Montserrat, sans-serif;
  font-size: 1rem;
  font-weight: 600;
  transition: all .3s;
}

header .btn:hover {
  color: #fff;
  background-color: #00bcd4;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px #00bcd44d;
}
